import { Router } from '@angular/router';
import { SelectivePreloadingStrategy } from './core/selective-preloading-strategy';
import { CanDeactiveGuard } from './core/can-deactive.guard';
import { AdminGuard } from './core/admin.guard';
import { BlogModule } from './blog/blog.module';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule} from '@angular/forms';

import { AngularFireModule } from 'angularfire2';
import { AngularFireAuthModule } from 'angularfire2/auth';

import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { AngularFontAwesomeModule } from 'angular-font-awesome';

import { AppComponent } from './app.component';
import { environment } from '../environments/environment';
import { AngularFontAwesomeComponent } from 'angular-font-awesome/src/component/angular-font-awesome.component';
import { AppRoutingModule } from './app-routing.module';
import { LoginComponent } from './login/login.component';
import { AuthService } from './core/auth.service';
import { AngularFireDatabaseModule } from 'angularfire2/database-deprecated';


@NgModule({
  declarations: [
    AppComponent,
    LoginComponent
  ],
  imports: [
    BrowserModule,
    ReactiveFormsModule,
    AngularFireModule.initializeApp(environment.firebase),
    AngularFireDatabaseModule,
    AngularFireAuthModule,
    NgbModule.forRoot(),
    AngularFontAwesomeModule,
    BlogModule,
    AppRoutingModule,

  ],
  bootstrap: [AppComponent],
  providers: [
    CanDeactiveGuard, SelectivePreloadingStrategy, AuthService, AdminGuard
  ]
})
export class AppModule {
  // Diagnostic only: inspect router configuration
  constructor(router: Router) {
    console.log('Routes: ', JSON.stringify(router.config, undefined, 2));
  }
 }



