import { SelectivePreloadingStrategy } from './core/selective-preloading-strategy';
import { CanDeactiveGuard } from './core/can-deactive.guard';
import { AdminGuard } from './core/admin.guard';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { AuthService } from './core/auth.service';

const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: 'home', loadChildren: 'app/blog/blog.module#BlogModule'},
  { path: 'admin', loadChildren: 'app/admin/admin.module#AdminModule', canLoad: [AdminGuard]},
  { path: 'login', component: LoginComponent}
];

@NgModule({
  imports: [ RouterModule.forRoot(routes,
    {
      enableTracing: true, // <-- debugging purposes only
      preloadingStrategy: SelectivePreloadingStrategy,

    }) ],
  exports: [ RouterModule ],
})
export class AppRoutingModule { }



